import React from 'react';
import PropTypes from 'prop-types';
import { createUseStyles } from 'react-jss';
import { useVideo, useMeasure } from 'react-use';
import { useStaticQuery, graphql } from 'gatsby';

import { useSlides } from './Context';

const useStyles = createUseStyles({
  root: {
    width: '100%',
    height: '100%',
    position: 'fixed',
    pointerEvents: 'none',
  },
  video: {
    width: '100%',
    height: '100%',
  },
}, { name: 'VideoSlide' });

const VideoSlide = ({
  component: Comp,
  name: baseName,
  autoNextSlide,
  className,
  autoPlay,
  ...props
}) => {
  const data = useStaticQuery(graphql`
    {
      allFile(filter: {extension: {eq: "mp4"}}) {
        nodes {
          name
          publicURL
        }
      }
    }
  `);
  const { onNextSlide } = useSlides();
  const [measureRef, {
    width,
    height,
    // x,
    // y,
    // top,
    // right,
    // bottom,
    // left,
  }] = useMeasure();

  const classes = useStyles({
    ...props,
    width,
    height,
    autoPlay,
    autoNextSlide,
  });

  let resolution = 360;
  if (width > 360) resolution = 720;
  if (width > 720) resolution = 1080;

  const searched = `${baseName}_${resolution}p`;
  const { publicURL: src } = data.allFile.nodes
    .find(({ name }) => name === searched) || {};

  // eslint-disable-next-line
  const [video, state, controls, ref] = useVideo(<video
    src={src}
    autoPlay={autoPlay}
    onEnded={() => {
      if (autoNextSlide) onNextSlide();
    }}
    className={classes.video}
  />);

  return (
    <Comp
      // eslint-disable-next-line react/jsx-props-no-spreading
      {...props}
      className={[className, classes.root].filter(Boolean).join(' ')}
      ref={measureRef}
    >
      {video}
    </Comp>
  );
};

VideoSlide.propTypes = {
  name: PropTypes.node.isRequired,
  component: PropTypes.oneOfType([
    PropTypes.elementType,
    PropTypes.string,
  ]),
  className: PropTypes.string,
  autoPlay: PropTypes.bool,
  autoNextSlide: PropTypes.bool,
};

VideoSlide.defaultProps = {
  component: 'div',
  className: null,
  autoPlay: true,
  autoNextSlide: true,
};

export default VideoSlide;
