import React from 'react';
import PropTypes from 'prop-types';
import AniLink from 'gatsby-plugin-transition-link/AniLink';
import { createUseStyles, useTheme } from 'react-jss';
import useSoundTrigger from './SoundController/useSoundTrigger';

import { styles as buttonStyles } from './Button';

const useStyles = createUseStyles((theme) => {
  const buttonBase = typeof styles === 'function'
    ? buttonStyles(theme) : buttonStyles;
  return {
    button: buttonBase.root,
  };
}, { name: 'Link' });

const Link = ({
  children,
  ...props
}) => {
  const theme = useTheme();
  const classes = useStyles(props);

  const {
    to,
    color,
    className,
    activeClassName,
    button,
  } = props;

  const soundProps = useSoundTrigger(props);

  return (
    <AniLink
      data-mk-link
      paintDrip
      hex={theme[color] || color}
      to={to}
      onClick={soundProps.onClick}
      onMouseEnter={soundProps.onMouseEnter}
      className={[
        className,
        classes.root,
        color && classes[color],
        button && classes.button,
      ].filter(Boolean).join(' ')}
      activeClassName={activeClassName || classes.active}
    >
      {children}
    </AniLink>
  );
};

Link.displayName = 'MKLink';

Link.propTypes = {
  children: PropTypes.node.isRequired,
  to: PropTypes.string.isRequired,
  color: PropTypes.string,
  className: PropTypes.string,
  activeClassName: PropTypes.string,
  onClick: PropTypes.func,
  onMouseEnter: PropTypes.func,
  button: PropTypes.bool,
};

Link.defaultProps = {
  color: 'primary',
  className: null,
  activeClassName: null,
  onClick: () => { },
  onMouseEnter: () => { },
  button: false,
};

export default Link;
